"use client";

import { useParams } from "next/navigation";
import categoryContent from "@/content/categoryData";

export default function EditorialCategory() {
  const params = useParams();
  const rawCategory = params?.category;

  // Ensure category is always a string
  const category = Array.isArray(rawCategory)
    ? rawCategory[0]
    : rawCategory || "";

  const decodedCategory = decodeURIComponent(category);

  console.log("Category from URL:", decodedCategory);
  console.log("Available categories:", Object.keys(categoryContent));

  // Type-safe object access
  const content =
    (categoryContent as Record<string, { title: string; description: string }>)[
      decodedCategory
    ] || null;

  if (!content) {
    return (
      <div className="max-w-4xl mx-auto p-6">
        <h1 className="text-2xl font-bold">Редакция: {decodedCategory}</h1>
        <p className="mt-4 text-red-500">Контент не найден</p>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h1 className="text-2xl font-bold">{content.title}</h1>
      <p className="mt-4 whitespace-pre-line">{content.description}</p>
    </div>
  );
}
